                                <p>1.6.  <b>Договор оказания услуг</b> – договор оказания услуг, заключенный (заключаемый) на условиях настоящей Оферты, между Покупателем и Агентом, в соответствии с которым Агентом оказываются услуги по бронированию и оформлению к реализации Билетов на Мероприятия, а Покупателем оплачиваются оказываемые услуги в размере номинальной стоимости Билета и сервисного сбора;</p>
