                                <p>1.10. <b>Мероприятие</b> – культурно-зрелищное мероприятие, в том числе театральный спектакль, цирковое представление, концерт в зале, клубе или на открытой площадке, выставка, кинопоказ, фестиваль, шоу, показ мод, спортивное соревнование, экскурсия, а также любое иное событие, посещение которого возможно только по предъявлению специального документа – Билета (Бланка строгой отчетности, Электронного билета);</p>
