                                <p>1.15. <b>Оферта</b> – настоящая публичная оферта ООО «KASSIR.KG» (ОГРН 1057747996151,  ИНН 7725547259, адрес местонахождения: 127055, г. Москва, ул. Новослободская, д.14/19, стр.1) на реализацию билетов на культурно-зрелищные мероприятия, опубликованная по электронному адресу в сети Интернет https://msk.kassir.ru/pages/oferta и размещенная в Точках продаж Агента в печатной версии.</p>
